import React from 'react';
import {S} from '../HederMenuStyles'




// const items = ['Home', 'Skills', 'Works', 'Testimony', 'Contact',]
const items = [
    {
        title: '',
        href: 'company'
    },
    {
        title:'',
        href: 'technologies'
    },
    {
        title: '',
        href: 'news'
    },
    {
        title: '',
        href: 'works'
    },

    {
        title: '',
        href: 'service'
    },
    {
        title: '',
        href: 'career'
    }
    ]
type Props = {
    onClick?: () => void;
};
 const Menu: React.FC<Props> = ({onClick}) => {
    return (

            <ul>
                {items.map((item, index) => {
                    return <S.MenuItem key={index}>
                        <S.NavLink activeClass='active'
                                   to={item.href}
                                   smooth={true}
                                   duration={250}
                                   delay={10}
                                   offset={-20}
                                   spy={true}
                                   onClick={onClick}
                        >

                            {item.title}



                        </S.NavLink>

                    </S.MenuItem>
                })

                }
            </ul>

    );
};

export default Menu;