import React from 'react';
import AliceCarousel from 'react-alice-carousel';
import 'react-alice-carousel/lib/alice-carousel.css';
import {S} from './SliderStyles-main'
import '../../styles/Slider.css'
import cctv from "../../assets/images/forSliderMain/cctv-2417559_1280.jpg"
import lotok from "../../assets/images/forSliderMain/___.png"
import pic1 from "../../assets/images/forSliderMain/1645790834_4.jpg"
import pic2 from "../../assets/images/forSliderMain/back02.png"
import pic3 from "../../assets/images/forSliderMain/slaid-3-m.jpg"
import pic4 from "../../assets/images/forSliderMain/slaid-2-m.jpg"







type SlidePropsType = {
    text: string;
    backgroundImage: string;

};


const Slide = (props: SlidePropsType) => {
    return (
        <S.Slide backgroundImage={props.backgroundImage}>

            <S.Text>{props.text}</S.Text>

        </S.Slide>
    );
};


const items = [
    <Slide text={'  '} backgroundImage={cctv} />,
    <Slide text={'    '} backgroundImage={lotok} />,
    <Slide text={'     '} backgroundImage={pic1} />,
    <Slide text={' ,     '} backgroundImage={pic2} />,
    <Slide text={'     '} backgroundImage={pic3} />,
    <Slide text={'  . '} backgroundImage={pic4} />,


];


export const SliderMain = () => {

    return <S.SliderStyled>
        <AliceCarousel
            mouseTracking
            items={items}
            autoPlay
            autoPlayInterval={2000}
            animationDuration={500}
            infinite={true}
            disableButtonsControls={false}  //  
            disableDotsControls={false}
            //  
        />
    </S.SliderStyled>
}


